.login__submit-button {
    margin: 216px 0 0 0;
    height: 50px;
    background: #FFFFFF;
    border-radius: 2px;
    padding: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
    transition: 0.3s;
}

.login__submit-button:hover {
  opacity: 0.8;
  cursor: pointer;
}

@media (max-width: 320px) {
  .login__submit-button {
    margin: 143px 0 0 0;
    height: 46px; 
  }
} 
